<script>
export default {
  props: {
    source: String
  },
  data() {
    return {
      ubs_selecionada: ''
    }
  },
  computed: {
    drawer() {
      return this.$store.getters.drawer
    },
  },
  methods: {
    sair() {
      sessionStorage.clear()
      this.$router.push('/')
    },

    verificaUsuarioLogado() {
      if(sessionStorage.perfil === undefined) {
        this.$router.push('/')
      }
    }
  },
  async created() {
    this.verificaUsuarioLogado()
    this.ubs_selecionada = JSON.parse(sessionStorage.ubs_selecionada)
    const self = this
    cordova.plugins.firebase.messaging.requestPermission().then(function() {
        cordova.plugins.firebase.messaging.getInstanceId().then(function(instanceId) {
            const instance_id = instanceId
            cordova.plugins.firebase.messaging.getToken().then(async function(token, instance_id) {
                await self.$axios.post('/push_notification', {
                  token: token,
                  plataforma: navigator.userAgent,
                  usuario_id: self.$store.getters.user.id,
                  instance_id: instance_id
                })
            })
        })
    })
    navigator.geolocation.getCurrentPosition()
  }
};
</script>

<style>
#keep .v-navigation-drawer__border {
  display: none;
}

::-webkit-scrollbar {
  width: 2px;
  border-radius: 10px;
  background: rgba(180, 179, 179, 0);
}
::-webkit-scrollbar-thumb {
  background: #F9A825;
  border-radius: 10px;
}

.menuAtivo {
  color: #ffffff;
  border-radius: 5px;
  margin-right: 5px;
  font-weight: bold;
}

  .router-anim-enter-active {
  animation: coming 0.4s;
  animation-delay: 0.4s;
  opacity: 0;
}

.router-anim-leave-active {
  animation: going 0.4s;
}

@keyframes going {
  from {
    scale: 1;
    opacity: 1;
  }
  to {
    scale: 0.7;
    opacity: 0.1;
  }
}

@keyframes coming {
  from {
    scale: 0.7;
    opacity: 0.1;
  }
  to {
    scale: 1;
    opacity: 1;
  }
}
</style>