<template>
    <div>
        <Molde title="Visualizar Funcionário" icon="description" :options="options">
            <div class="expande-horizontal">
                <v-flex xs12 class="pa-3 ">
                    <v-form ref="form">
                        <v-flex xs12>
                            <v-text-field :rules="[v => !!v || 'Preencha este campo']" :color="$primaria" class="mr-2 ml-2" v-model="dados.nome" outlined required  label="Nome" ></v-text-field>
                            <v-text-field :rules="[v => !!v || 'Preencha este campo']" :color="$primaria" v-mask="['###.###.###-##']" class="mr-2 ml-2" v-model="dados.cpf" outlined required  label="Cpf" ></v-text-field>
                        </v-flex>
                        
                        <div class="expande-horizontal semquebra">
                            <v-text-field :rules="[v => !!v || 'Preencha este campo']" :color="$primaria" class="mr-2 ml-2" v-model="dados.email" outlined required  label="email" ></v-text-field>
                            <v-text-field :color="$primaria" class="mr-2 ml-2" v-model="dados.senha" outlined required  label="senha" ></v-text-field>
                        </div>

                        <div class="expande-horizontal semquebra">
                            <v-text-field :rules="[v => !!v || 'Preencha este campo']" :color="$primaria" class="mr-2 ml-2" v-model="dados.endereco" outlined required  label="Endereço" ></v-text-field>
                            <v-text-field :rules="[v => !!v || 'Preencha este campo']" :color="$primaria" class="mr-2 ml-2" v-model="dados.numero_casa" outlined required  label="Número da casa" ></v-text-field>
                        </div>

                        <v-flex xs12 class="pa-2">
                            <v-select
                                :items="permissao"
                                v-model="dados.permissao_id"
                                item-text="nome"
                                v-if="false"
                                :return-object="false"
                                item-value="_id"
                                :rules="[v => !!v || 'Selecione um perfil de permissão para este funcionário']"
                                outlined
                                label="Permissão de permissão do funcionário"
                            />
                        </v-flex>

                        <v-btn @click="save" class="text-capitalize white--text ml-2" color="green"> Concluir <v-icon>check</v-icon> </v-btn>
                    </v-form>
                </v-flex>
            </div>  
        </Molde>
    </div>
</template>

<script>
import { mask } from 'vue-the-mask'
export default {
    props: ['id'],
    directives: {
        mask
    },
    data() {
        return {
            options: [
                {
                    icon: "save",
                    nome: "Salvar Alterações",
                    action: () => this.save()
                }
            ],
            dados: {},
            permissao: []
        }
    },
    methods: {
        async save() {
            if(this.$refs.form.validate()) {
                await this.$update(`funcionario/${this.id}`, this.dados)
                this.$router.go(-1)
            }
        },
    },
    async created() {
        this.dados = await this.$show(`funcionario/${this.id}`)
        //this.permissao = await this.$list('permissao')
    }
}
</script>

