<template>
    <div>
        <Molde title="Visualizar O.S." icon="description" :options="options">
            <div class="expande-horizontal">
                <v-flex xs12 class="pa-3 ">
                    <v-form ref="form">
                        <div class="expande-horizontal semquebra">
                            <v-autocomplete
                                readonly
                                class="mr-2 ml-2"
                                :color="$primaria"
                                outlined
                                v-model="cliente_selecionado"
                                :items="cliente"
                                label="Cliente"
                                return-object
                                placeholder="ex: Michel"
                                item-text="nome"
                            />
                        </div>
                        
                        <div class="expande-horizontal semquebra">
                            <v-autocomplete
                                readonly
                                class="mr-2 ml-2"
                                :color="$primaria"
                                outlined
                                v-model="responsavel"
                                :items="funcionario"
                                label="Funcionário responsável por executar a ordem"
                                return-object
                                placeholder="ex: Ricardo"
                                item-text="nome"
                            />
                        </div>
                        
                        <v-flex xs12>
                            <v-text-field readonly :color="$primaria" class="mr-2 ml-2" v-model="dados.endereco" outlined required label="Endereço" />
                        </v-flex>

                        <div class="expande-horizontal semquebra">
                            <v-menu
                                v-model="menu"
                                :close-on-content-click="false"
                                transition="scale-transition"
                                offset-y
                                max-width="290px"
                                min-width="290px"
                                >
                                <template v-slot:activator="{ on }">
                                    <v-text-field
                                    outlined
                                    :color="$primaria"
                                    v-model="computedDateFormatted"
                                    label="Data"
                                    class="ml-2 mr-2"
                                    append-icon="event"
                                    readonly
                                    v-on="on"
                                    ></v-text-field>
                                </template>
                                <v-date-picker readonly locale="pt-br" v-model="date" no-title @input="menu = false"></v-date-picker>
                            </v-menu>
                            <v-text-field readonly :color="$primaria" class="mr-2 ml-2" v-mask="['##:##']" v-model="dados.hora" outlined required label="Hora" />
                            <v-text-field readonly :color="$primaria" class="mr-2 ml-2" v-model="dados.contato" outlined required label="Obs" />
                        </div>

                            <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.modulos" outlined required label="Quantidade de módulos" />
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.marca" outlined required label="Marca/Modelo" />
                        </div>
                        
                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.inversores" outlined required label="Inversores" />
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.stringbox" outlined required label="String Box" />
                        </div>
                        
                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.mppts" outlined required label="Mppt's" />
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.telhado" outlined required label="Telhado" />
                        </div>
                        
                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.arranjos" outlined required label="Arranjos" />
                        </div>

                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.cabeamento" outlined required label="Cabeamento" />
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.disjuntor" outlined required label="Disjuntor" />
                        </div>
                        
                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.canaleta" outlined required label="Canaleta" />
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.eletroduto" outlined required label="Eletroduto" />
                        </div>
                        
                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.haste_terra" outlined required label="Haste Terra" />
                        </div>
                        
                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.inclinacao" outlined required label="Inclinação" />
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.localizacao" outlined required label="Localização" />
                        </div>
                        
                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.condutor" outlined required label="Condutor" />
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.compras" outlined required label="Compras" />
                        </div>

                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.saida" outlined required label="Saída" />
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.chegada" outlined required label="Chegada" />
                        </div>

                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.km_saida_adtina" outlined required label="KM saída adtina" />
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.km_saida_cliente" outlined required label="KM saída cliente" />
                        </div>
                        
                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.km_chegada_adtina" outlined required label="KM chegada adtina" />
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.km_chegada_cliente" outlined required label="KM chegada cliente" />
                        </div>

                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.horario_saida_adtina" outlined required label="Horário saída adtina" />
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.horario_saida_cliente" outlined required label="Horário saída cliente" />
                        </div>

                        <div class="expande-horizontal semquebra">
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.horario_chegada_adtina" outlined required label="Horário chegada adtina" />
                            <v-text-field dense :color="$primaria" class="mr-2 ml-2" v-model="dados.horario_chegada_cliente" outlined required label="Horário chegada cliente" />
                        </div>

                        <div class="expande-horizontal semquebra">
                            <v-textarea :color="$primaria" class="mr-2 ml-2" v-model="dados.ajudantes" outlined required label="Ajudantes" />
                        </div>

                    </v-form>
                </v-flex>
            </div>
        </Molde>
    </div>
</template>

<script>
import { mask } from 'vue-the-mask'

export default {
    props: ['id'],
    directives: {
        mask
    },
    data() {
        return {
            dialogo_de_encerramento: false,
            tab: 0,
            options: [
            ],
            ordem_material: [],
            ordem_equipamento: [],
            ordem_carro: [],
            ordem_funcionario: [],
            dados: {
                data: this.formatDate(new Date().toISOString().substr(0, 10))
            },
            cliente: [],
            cliente_selecionado: '',
            funcionario: [],
            materiais_utilizados: [],
            carros_utilizados: [],

            responsavel: {},
            date: new Date().toISOString().substr(0, 10),
            menu: false,
        }
    },
    computed: {
      computedDateFormatted () {
        return this.formatDate(this.date)
      },
    },
    watch: {
        cliente_selecionado(val) {
            this.dados.cliente = val
            this.dados.cliente_id = val._id
        },
        responsavel(val) {
            this.dados.responsavel = val
            this.dados.responsavel_id = val._id
        },
        date (val) {
            this.dados.data = this.formatDate(this.date)
        },
    },
    methods: {
        async antesDeEncerrar() {
            this.materiais_utilizados = await this.$list(`ordem_material/${this.id}`)
            this.carros_utilizados = await this.$list(`ordem_carro/${this.id}`)
            this.dialogo_de_encerramento = true
        },
        dogLocation() {
            setInterval(() => {
                if(this.dados.status === 'em andamento') {
                    this.getPosition()
                }
            } , 10000)
        },
        async encerraOrdem() {
            if(this.$refs.form.validate()) {

                await this.$update(`ordem/${this.id}`, { status: 'encerrada', hora_encerramento: this.$moment().format('DD/MM/YYYY [às] HH:mm') })
                
                this.materiais_utilizados.map(async material => {
                    await this.$update(`ordem_material/${material._id}`, { quantidade_utilizada: material.quantidade_utilizada })
                })
                
                this.carros_utilizados.map(async carro => {
                    await this.$update(`carro/${carro.carro._id}`, { quilometragem_atual: carro.quilometragem_atual })
                })

                const self = this

                self.$db.ref(`notifications`).push({
                    usuario: self.$store.getters.user,
                    dados: self.dados,
                    message: `${self.$store.getters.user.nome} -> Ordem encerrada -> ${self.$moment().format('DD/MM/YYYY [às] HH:mm')}` 
                })

                await this.$update(`ordem_material/${this.id}`, { status: 'encerrada', hora_encerramento: this.$moment().format('DD/MM/YYYY [às] HH:mm') })

                this.$db.ref(`/locations/${self.$store.getters.user._id}`).remove()
                    .then(() => console.log('sucesso'))
                    .catch(e => console.log('erro', e))

                location.reload()
            }

        },
        async ordemEmAndamento() {
            await this.$update(`ordem/${this.id}`, { status: 'em andamento' , hora_inicio: this.$moment().format('DD/MM/YYYY [às] HH:mm')})

            const self = this

            self.$db.ref(`notifications`).push({
                usuario: self.$store.getters.user,
                dados: self.dados,
                message: `${self.$store.getters.user.nome} -> Ordem em andamento -> ${self.$moment().format('DD/MM/YYYY [às] HH:mm')}` 
            })

            this.dogLocation()
            
            this.$router.go(-1)
        },
        async pausaOrdem() {
            await this.$update(`ordem/${this.id}`, { status: 'pausado' })

            const self = this

            self.$db.ref(`notifications`).push({
                usuario: self.$store.getters.user,
                dados: self.dados,
                message: `${self.$store.getters.user.nome} -> Ordem em pausa -> ${self.$moment().format('DD/MM/YYYY [às] HH:mm')}` 
            })

            this.$router.go(-1)
        },
        async get(collection, id) {
            this[`${collection}`] = await this.$list(`${collection}/${id}`)
        },
        async save() {
            if(this.$refs.form.validate()) {
                await this.$update(`ordem/${this.id}`, this.dados)
                this.$router.go(-1)
            }
        },
        formatDate (date) {
            if (!date) return null

            const [year, month, day] = date.split('-')
            return `${day}/${month}/${year}`
        },
        getPosition() {
            const self = this

            if(navigator.geolocation) {
                navigator.geolocation.getCurrentPosition(self.showPosition, self.showError, {timeout: 30000, enableHighAccuracy: true, maximumAge: 75000});
            } else {
                this.$store.dispatch('snackbar_info', 'Este browser não tem suporte a geolocalização ou o gps está desligado.')
            }
        },
        showPosition(position) {
            const self = this

            self.$db.ref(`/locations/${self.$store.getters.user._id}`).set({
                coordinates: [position.coords.longitude, position.coords.latitude],
                usuario: self.$store.getters.user,
                dados: self.dados
            })
            

        },
        showError(error) {
            const self = this
            self.$store.dispatch('snackbar_info', error.message)
            //self.$store.dispatch('snackbar_info', 'Desculpe, não conseguimos as permissões necessárias para ver sua localização, vá até configurações/aplicativos procure o app iluz solar/permissões e ative a localização.')
            //switch(error.code) {
            //    case error.PERMISSION_DENIED:
            //        self.$store.dispatch('snackbar_info', 'A permissão para pegar a localização foi negada pelo usuário.')
            //    break;
            //    case error.POSITION_UNAVAILABLE:
            //        self.$store.dispatch('snackbar_info', 'As informações de localização não estão disponíveis neste dispositivo, verifique se o GPS está ativo.')
            //    break;
            //    case error.TIMEOUT:
            //        self.$store.dispatch('snackbar_info', 'O tempo de espera pela localização se esgotou')
            //    break;
            //    case error.UNKNOWN_ERROR:
            //        self.$store.dispatch('snackbar_info', 'Erro, atualize a págine')
            //    break;
            //}
        }

    },
    async created() {
        const dados = await this.$show(`ordem/${this.id}`)
        this.dados = dados

        this.cliente = await this.$list('cliente')
        this.funcionario = await this.$list('funcionario')

        this.cliente_selecionado = dados.cliente
        this.responsavel = dados.responsavel
        this.dogLocation()
    }
}
</script>

