import Vue from "vue";
import Router from "vue-router";

/* Views aqui */
import Login from "@/views/Login.vue";
import Home from "@/views/Home.vue";
import Cliente from "@/views/Cliente.vue";

//home
import { funcionario_home } from './home/funcionario'
import { ficha_home } from './home/ficha'
import { painel_home } from './home/painel'
import { notificacao_home } from './home/notificacao'
import { prontuario_home } from './home/prontuario'
import { ubs_home } from './home/ubs'

//cliente
import { contrato_cliente } from './cliente/contrato'
import { ordem_cliente } from './cliente/ordem'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "login",
      component: Login
    },
    {
      path: "/home",
      name: "home",
      component: Home,
      children: [
        ...funcionario_home,
        ...ficha_home,
        ...painel_home,
        ...notificacao_home,
        ...prontuario_home,
        ...ubs_home
      ]
    },
    {
      path: "/cliente",
      name: "cliente home",
      component: Cliente,
      children: [
        ...contrato_cliente,
        ...ordem_cliente
      ]
    },
  ]
});
