<template>
  <v-container style="flex-direction: column; background: rgba(0,0,0,0) " fluid class="molde pa-0 ma-0">
    <v-layout column>
       
    <v-card :elevation="$vuetify.breakpoint.smAndDown ? '0' : '0'" class="pa-0 ma-0">

      <v-app-bar :fixed="$vuetify.breakpoint.smAndDown" :color="$vuetify.breakpoint.smAndDown ? 'white' : 'white'" class="elevation-2" :style="$vuetify.breakpoint.smAndDown ? 'margin-bottom: 0px; border-radius: 0px' : 'border-radius: 0px;margin-bottom: 0px'" style="justify-content: center; align-items: center;" >
        
        <v-btn v-if="this.icon === undefined" @click="$store.commit('up_drawer')" icon>
          <v-avatar class="elevation-0">
            <v-icon :color="$primaria">menu</v-icon>
          </v-avatar>
        </v-btn>

        <v-btn v-if="this.icon !== undefined" @click="goBack()" icon>
          <v-avatar class="elevation-0">
            <v-icon :color="$primaria"> arrow_back </v-icon>
          </v-avatar>
        </v-btn>

        <h3 :class="$vuetify.breakpoint.smAndDown ? 'font-weight-bold' : ''" :style="`color: ${$primaria}`">{{ title }}</h3>

        <v-spacer></v-spacer>

        <v-btn small v-for="item in options" :color="$primaria" class="hidden-sm-and-down animated flipInX delay-1s mr-1" @click="item.action()" :key="item.nome">
            <v-icon class="mr-1" color="#fff"> {{ item.icon }} </v-icon>
            <span class="white--text text-capitalize" >{{ item.nome}}</span> 
        </v-btn>

        <v-menu
          class="hidden-lg-and-up"
          left
          bottom
        >
          <template v-slot:activator="{ on }">
            <v-btn class="hidden-lg-and-up" icon v-on="on">
              <v-icon :color="$vuetify.breakpoint.smAndDown ? 'grey darken-2' : '#000'">more_vert</v-icon>
            </v-btn>
          </template>

          <v-list>
            <v-list-item v-for="item in menu" :key="item.nome" @click="() => item.action()">
              <v-list-item-title>{{ item.nome }}</v-list-item-title>
            </v-list-item>
            <v-list-item v-for="item in options" :key="item.nome" @click="() => item.action()">
              <v-list-item-title>{{ item.nome }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-app-bar>

      <v-divider></v-divider>

      <!-- Conteúdo enviado via slot -->
      <div :class="$vuetify.breakpoint.smAndDown ? 'mt-12 pt-2' : ''" class="molde">
        <v-flex xs12>
          <slot></slot>
        </v-flex>
      </div>

    </v-card>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  props: ['title', 'menu', 'options', 'icon', 'action', 'origem'],
  data() {
    return {
      pesquisa: false,
      fab: true
    }
  },
  methods: {
    goBack() {
      if(this.action !== undefined) {
        this.action()
      } else {
        this.$router.go(-1)
      }
    }
  },
  mounted() {
    console.log('menu', this.menu)
  }
}
</script>

<style>
  .centraliza-tudo {
    display: flex;
    flex-direction: row;
    align-items: center
  }

  .molde {
    display: flex;
    width: 100%;
  }
</style>