<template>
    <div style="min-height: 90vh">
      <v-simple-table v-if="dados.length > 0" >
        <template v-slot:default>
          <thead>
            <tr>
              <th>Parecer</th>
              <th>Data e hora</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in dados" :key="item._id">
              <td class="font-weight-bold fonteCorpo">{{ item.mensagem }}</td>
              <td class="font-weight-bold fonteCorpo grey--text">{{ $moment(item.created_at).format('DD/MM/YYYY [às] HH:mm') }}</td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
      <Empty v-else ></Empty>
    </div>
</template>

<script>
export default {
  props: ['dados'],
  methods: {
    async remove(val) { await this.$del(`ficha_diario_bordo/${val._id}`) }
  }
}
</script>