<template>
  <Molde title="Prontuários" :options="options" >
    <div style="min-height: 90vh">
        <v-flex class="pa-2 pt-4" xs12 md6>
            <div class="expande-horizontal semquebra">
                <v-menu
                ref="menu"
                v-model="menu"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="290px"
                >
                    <template v-slot:activator="{ on }">
                        <v-text-field
                        outlined
                        dense
                        v-model="data_a_carregar"
                        label="DATA A BUSCAR"
                        append-icon="event"
                        :color="$primaria"
                        readonly
                        :hint="$moment(data_a_carregar).locale('pt-br').format('LL')"
                        :persistent-hint="true"
                        v-on="on"
                        ></v-text-field>
                    </template>
                    <v-date-picker
                        outlined
                        dense
                        :color="$primaria"
                        ref="picker"
                        locale="pt-br"
                        v-model="data_a_carregar"
                        :max="new Date().toISOString().substr(0, 10)"
                        min="1950-01-01"
                        @change="save"
                    ></v-date-picker>
                </v-menu>
                <v-btn @click="getProntuarios" large class="ml-2 mr-2" :color="$primaria">Buscar</v-btn>
            </div>
        </v-flex>
      <v-simple-table v-if="dados.length > 0 && !$vuetify.breakpoint.smAndDown" >
        <template v-slot:default>
          <thead>
            <tr>
              <th><v-icon :color="$primaria">ballot</v-icon></th>
              <th>ORDEM</th>
              <th>PACIENTE</th>
              <th>DATA</th>
              <th>VISUALIZAR</th>
              <th v-if="false">EXCLUIR</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in dados" :key="item._id" class="animated fadeInUp">
              <td style="width: 20px"> <v-icon :color="$primaria">ballot</v-icon> </td>
              <td style="width: 20px" class="font-weight-bold fonteCorpo">{{ item.ordem }}</td>
              <td class="font-weight-bold fonteCorpo">{{ item.ficha.nome }}</td>
              <td class="font-weight-bold fonteCorpo">{{ item.data }} às {{ item.hora }}</td>
              <td> <v-btn outlined class="text-lowercase" :color="$primaria" @click="$router.push(`/home/visualizar-ficha/${item.ficha._id}`)"> visualizar</v-btn> </td>
              <td v-if="false"> <v-btn outlined class="text-lowercase" color="red" @click="remover(item._id)"> excluir</v-btn> </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
      <Empty v-if="dados.length === 0" ></Empty>
      <v-divider></v-divider>
      <v-list class="pa-0 ma-0" v-if="dados.length > 0 && $vuetify.breakpoint.smAndDown">
        <template v-for="(item, index) in dados">
          <v-list-item class="animated fadeInUp" @click="$router.push(`/home/visualizar-ficha/${item.ficha._id}`)" :key="item._id">
            <v-list-item-content>
              <v-list-item-title class="fonteCorpo" style="color: #444"> {{ item.ordem }} - {{ item.ficha.nome }} </v-list-item-title>
              <v-list-item-subtitle class="fonteCorpo grey--text"> {{ item.ficha.endereco }} </v-list-item-subtitle>
            </v-list-item-content>
            <v-list-item-action>
              <v-icon :color="$primaria">keyboard_arrow_right</v-icon>
            </v-list-item-action>
          </v-list-item>
          <v-divider :key="index"></v-divider>
        </template>
      </v-list>
    </div>
    <Confirm :open="confirm_dialog" :action="() => this.remover(this.id_to_excluse, true)" :close="() => this.confirm_dialog = false" />
  </Molde>
</template>

<script>
export default {
  data() {
    return {
        pesquisa: '',
        options: [
        ],
        dados: [],
        dadosbkp: [],
        confirm_dialog: false,
        id_to_excluse: '',
        data_a_carregar: null,


    }
  },
  watch: {
    pesquisa(val) {
      this.pesquisar(val)
    },
    menu (val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = 'YEAR'))
    },
  },
  filters: {
      filterBlock(val) {
        if(parseInt(val) === 1) {
          return 'bloqueado'
        } else {
          return 'ativo'
        }
      },
      filterSaldo(val) {
        return parseFloat(val).toFixed(2)
      }
  },
  methods: {
    getProntuarios() {
        this.$store.dispatch('up_charging')

        const data = {
            data_a_buscar: this.data_a_carregar,
            ubs_a_buscar: JSON.parse(sessionStorage.ubs_selecionada),
            ubs_a_buscar_id: JSON.parse(sessionStorage.ubs_selecionada)._id
        }

        this.$axios.post('/prontuarios', data)
            .then(res => {
                this.dados = res.data
                this.dadosbkp = res.data
                this.$store.dispatch("down_charging")
            })
            .catch(e => {
                this.$store.dispatch("snackbar_info", e.message)
                this.$store.dispatch("down_charging")
            })
    },
    async remover(id, confirmacao) {
      if(confirmacao) {
        await this.$del(`ubs/${id}`)
        this.get()
      } else {
        this.confirm_dialog = true
        this.id_to_excluse = id
      }
    },
    pesquisar(val) {
      const filtro = val

      if(val === '') {
        this.dados  = this.dadosbkp
      } else {
        const filtroTotal = this.dadosbkp
        let filtrados = []
        let filtroUpperCase = filtro.toUpperCase()
        filtroTotal.map(item => {
          let dadoUpperCase = item.nome.toUpperCase()
          if (!!dadoUpperCase.match(filtroUpperCase)) {
            filtrados.push(item)
          }
        })
        this.dados = filtrados
      }
    }
  },
  created() {
    this.$store.dispatch('up_charging')
    this.data_a_carregar = this.$moment().format('YYYY-MM-DD')

    const data = {
        data_a_buscar: this.$moment().format('YYYY-MM-DD'),
        ubs_a_buscar: JSON.parse(sessionStorage.ubs_selecionada),
        ubs_a_buscar_id: JSON.parse(sessionStorage.ubs_selecionada)._id
    }

    this.$axios.post('/prontuarios', data)
    .then(res => {
        this.dados = res.data
        this.dadosbkp = res.data
        this.$store.dispatch("down_charging")
    })
    .catch(e => {
        this.$store.dispatch("down_charging")
        this.$store.dispatch("snackbar_info", e.message)
    })
  },
}
</script>