<template>
    <div>
        <Molde title="Visualizar Ubs" icon="description" :options="options">
            <div class="expande-horizontal">
                <v-flex xs12 class="pa-3 ">
                    <v-form ref="form">
                        <v-flex xs12>
                            <v-text-field :rules="[v => !!v || 'Preencha este campo']" :color="$primaria" class="mr-2 ml-2" v-model="dados.nome" outlined required  label="Nome" ></v-text-field>
                        </v-flex>                        

                        <v-btn @click="save" class="text-capitalize white--text ml-2" color="green"> Salvar <v-icon>check</v-icon> </v-btn>
                    </v-form>
                </v-flex>
            </div>  
        </Molde>
    </div>
</template>

<script>
import { mask } from 'vue-the-mask'
export default {
    props: ['id'],
    directives: {
        mask
    },
    data() {
        return {
            options: [
                {
                    icon: "save",
                    nome: "Salvar Alterações",
                    action: () => this.save()
                }
            ],
            dados: {},
        }
    },
    methods: {
        async save() {
            if(this.$refs.form.validate()) {
                await this.$create('ubs', this.dados)
                this.$router.go(-1)
            }
        },
        async get() {
            this.dados = await this.$show(`ubs/${this.id}`)
        }
    },
    created() {
        this.get()
    }
}
</script>

