<template>
  <Molde title="Fichas" :options="options">
    <!-- <div>
      <v-progress-linear
        v-model="porcentagem"
        color="green"
        height="18"
      >
          <strong class="white--text">carregando {{ dados.length }} de {{ total_docs }} fichas</strong>
      </v-progress-linear>
    </div> -->
    <div class="expande-horizontal pa-3">
      <v-card dark color="deep-purple" class="pa-2 expande-horizontal centraliza coluna" style="border: 1px solid #e2e2e2; border-radius: 6px">
        <h5 class="ma-2 pb-4 text-center font-weight-bold text-uppercase white--text">filtros de busca - {{ total_fichas }} ficha(s) encontrada(s) </h5>
        <div class="mt-2 centraliza expande-horizontal">
          <v-flex class="px-2" xs12 md3>
            <v-select outlined dense autofocus autocomplete="new-login" :color="$primaria" return-object item-text="nome" v-model="ubs_pesquisa" :items="ubs_disponiveis" label="Dados para:" />
          </v-flex>
          <v-flex class="px-2" xs12 md3>
            <v-text-field @keypress.enter="get_paginacao(1)" dense :hint="`${total_fichas} encontrados.`" v-model="pesquisa" solo solo-inverted label="Pesquise um nome" :color="$primaria" append-icon="search"></v-text-field>
          </v-flex>
          <v-flex @keypress.enter="get_paginacao(1)" class="px-2" xs12 md3>
            <v-text-field v-mask="['##/##/####']" dense :hint="`${total_fichas} encontrados.`" v-model="pesquisa_data" solo solo-inverted label="Pesquise pela data" :color="$primaria" append-icon="event"></v-text-field>
          </v-flex>
          <v-switch class="pb-2" color="white" label="Apenas pendentes" v-model="pesquisa_pendentes"></v-switch>
        </div>
        <div class="expande-horizontal centraliza coluna semquebra">
          <v-btn @click="get_paginacao(1)" color="green" class="mr-2 white--text">buscar [ENTER]</v-btn>
          <v-btn @click="zera_filtro" class="mr-2" text color="white">zerar filtros</v-btn>
        </div>

        <!-- <v-divider class="mb-2"></v-divider> -->

        <!-- <span class="ma-2 grey--text">Buscar pelo cpf</span>

        <div class="pr-2 pl-2 expande-horizontal semquebra">
          <v-text-field @keyup.enter="buscar_cpf" :color="$primaria" outlined label="buscar pelo cpf" v-mask="['###.###.###-##']" v-model="busca_pelo_cpf"></v-text-field>
          <v-btn color="green" class="ml-2 white--text" large @click="buscar_cpf">buscar</v-btn>
        </div> -->
      </v-card>
    </div>

    <v-row v-if="fichas_atuais.length > 0">
      <v-col cols="12">
        <v-simple-table dense style="height: 100%; overflow: auto">
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left subtitle-2">PACIENTE</th>
                <th class="text-left subtitle-2">CPF</th>
                <th class="text-left subtitle-2">TIPO DE TESTE</th>
                <th class="text-left subtitle-2">RESULTADO</th>
                <th class="text-left subtitle-2">SITUAÇÃO</th>
                <th class="text-left font-weight-bold subtitle-2">AÇÕES</th>
              </tr>
            </thead>

            <tbody>
              <tr v-for="(item, index) in fichas_atuais" :key="item._id" class="animated fadeInUp">
                <td class="fonteCorpo font-weight-light text-uppercase">{{ item.nome }} </td>
                <td>
                  <span class="fonteCorpo font-weight-light text-uppercase">{{ item.cpf }}</span>
                </td>
                <td>
                  <span class="fonteCorpo font-weight-light text-lowercase">{{ item.tipo_de_teste  }}</span>
                </td>
                <td>
                  <span class="fonteCorpo text-lowercase" :style="`color: ${calcula_cor(item.resultado_do_teste)}`">{{ item.resultado_do_teste  }}</span>
                </td>
                <td>
                  <span class="fonteCorpo font-weight-light text-lowercase">{{ item.estado_do_teste  }}</span>
                </td>
                <td>
                  <v-btn
                    color="yellow darken-3"
                    class="pa-0 ma-0 font-weight-light white--text"
                    text
                    @click="() => $router.push('/home/visualizar-ficha/'+item._id)"
                  >Visualizar
                    <v-icon>remove_red_eye</v-icon>
                  </v-btn>
                  <v-btn
                    color="red darken-2"
                    class="white--text ml-2"
                    small
                    @click.prevent="remover(item)"
                    v-if="$store.getters.user.admin"
                  >Excluir</v-btn>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-col>
      <div class="expande-horizontal centraliza">
        <v-pagination
            total-visible="15"
          :color="$primaria"
          v-model="page"
          :length="tamanho_paginacao"
          @input="get_paginacao($event)"
        ></v-pagination>
      </div>
    </v-row>
    <Empty v-else></Empty>
  </Molde>
</template>

<script>
import { mask } from 'vue-the-mask'

export default {
  directives: {
    mask
  },
  data() {
    return {
      ubs_pesquisa: {
        nome: 'GERAL'
      },
      ubs_disponiveis: [],
      pesquisa_pendentes: sessionStorage.pesquisar_pendentes,
      tamanho_paginacao: 1,
      porcentagem: 0,
      total_fichas: parseInt(sessionStorage.total_docs),
      page: sessionStorage.page,
      fichas_atuais: [],
      dados: [],
      dadosbkp: [],
      total_docs: parseInt(sessionStorage.total_docs),
      pagina_atual: [],
      pesquisa: sessionStorage.pesquisa,
      pesquisa_data: sessionStorage.pesquisa_data,
      busca_pelo_cpf: '',
      icone: "person",
      options: [
        {
            icon: 'replay',
            nome: 'Atualizar',
            action: () => this.atualizar()   
        },
        {
            icon: 'add',
            nome: 'Criar Ficha',
            action: () => this.$router.push('/home/criar-ficha')    
        }
      ]
    };
  },
//   computed: {
// 		pesquisa() {
// 			return this.$store.getters.pesquisa
// 		}
// 	},
	watch: {
    pesquisa_pendentes(val) {
      if(!!val) {
        this.pesquisar_pendentes()
        sessionStorage.pesquisar_pendentes = val
      } else {
        this.zera_filtro()
      }
    },
    pesquisa_data(termoBusca) {
      // this.pesquisar(termoBusca)
      sessionStorage.pesquisa_data = termoBusca
    },
		pesquisa(termoBusca) {
      sessionStorage.pesquisa = termoBusca
      // this.pesquisar(termoBusca)
      //this.pesquisar(val)
    },
    page(val) {
      sessionStorage.page = val
      // this.change_number_page(val)
    }
	},
  methods: {
    seleciona_paginacao(e) {
      console.log(e)
    },
    buscar_cpf() {
      this.$store.dispatch('up_charging')

      const dados = {
        cpf: this.busca_pelo_cpf
      }

      this.$axios.post('/contulta_ficha', dados)
        .then((res) => {
          if(res.data.length > 0) {
            //tem ficha
            this.$router.push('/home/visualizar-ficha/'+res.data[0]._id)
            this.$store.dispatch('down_charging')
          } else {
            //não tem ficha
            this.$store.dispatch('snackbar_info', 'não há ficha para este cpf')
            this.$store.dispatch('down_charging')
          }
        })
        .catch(e => {
          this.$store.dispatch('snackbar_error', e.response.data.message)
          this.$store.dispatch('down_charging')
        })
    },
    zera_filtro() {
      this.pesquisa_pendentes = false
      this.pesquisa_data = ''
      this.pesquisa = ''
      this.dados = this.dadosbkp
      this.get_paginacao(1)
      // this.calculaPage(this.dadosbkp)
    },
      calcula_cor(cor) {
          let retorno = ''
          if(!! cor && cor === 'IGM POSITIVO') {
              retorno = 'green'
          } else {
              retorno = 'red'
          }
          return retorno
      },
      atualizar() {
          sessionStorage.removeItem('fichas')
          this.get()
      },
    pesquisar(val) {
      const filtro = val

      if(val === '') {
        this.dados  = this.dados
      } else {
        const filtroTotal = this.dadosbkp
        let filtrados = []
        let filtroUpperCase = filtro.toUpperCase()
        filtroTotal.map(item => {
          let dadoUpperCase = !!item.nome ? item.nome.toUpperCase() : ''
          if (!!dadoUpperCase.match(filtroUpperCase)) {
            filtrados.push(item)
          } else if (!!item.data_da_notificacao.match(filtroUpperCase)) {
            filtrados.push(item)
          }
        })
        this.dados = filtrados
        this.calculaPage(filtrados)
      }
    },
    pesquisar_pendentes() {
        const filtroTotal = this.dados
        let filtrados = []
        filtroTotal.map(item => {
          console.log('e', item)
          if(!item.resultado_do_teste || item.estado_do_teste === 'Coletado') {
            filtrados.push(item)
          } 
        })
        this.dados = filtrados
        this.calculaPage(filtrados)
    },
    change_number_page(val) {
      const clientes = this.dados
      const entre1 = (parseInt(val) - 1) * 20
      const entre2 = parseInt(val) * 20
      let pagina_atual = clientes.filter((cliente, index) => (index >= entre1 && index <= entre2))
      this.pagina_atual = pagina_atual

    },
    filtro_page(index) {
      let page = this.page
      let mostra = false
      let indice_min = (page * 10) - 10
      let indice_max = (page * 10) + 10
      if(this.dados.length > 3) {
        if(index <=  indice_min && index >= indice_max) {
          mostra = true
        }
      } else {
        mostra = true
      }
      return mostra
    },
    get() {
        if(!!sessionStorage.fichas) {
            this.dados = JSON.parse(sessionStorage.fichas)
            this.dadosbkp = JSON.parse(sessionStorage.fichas)
            this.calculaPage(JSON.parse(sessionStorage.fichas))
        } else {
            this.$store.dispatch("up_charging");
            this.$axios
              .get("/ficha")
              .then(res => {
                    sessionStorage.fichas = JSON.stringify(res.data)
                    this.$store.dispatch("down_charging");
                    this.dados = res.data;
                    this.dadosbkp = res.data;
                    this.calculaPage(res.data)
              })
              .catch(e => {
                    this.$store.dispatch("down_charging");
                    this.$store.dispatch("snackbar_error", e.response.data.message);
              });
        }
    },
    calculaPage(dados) {
      this.tamanho_paginacao = parseInt(dados.length / 20)
      this.change_number_page(this.page)
    },
    async remover(item) {
      //carrega isso V
      await this.$del(`ficha/${item._id}`);
      await this.get();
    },
    normaliza_page() {
      let dados = []
      let numero_pages = parseInt(sessionStorage.page_atual)
      for(let i=1;; i++ ) {
        if(i <= numero_pages) {
          let pre_dados =  JSON.parse(this.$storage.get(`page_${i}`).dados)
          dados = [...dados, ...pre_dados]
        } else {
          let dados_cadastrados = !!this.$storage.has('dados_cadastrados') ? JSON.parse(this.$storage.get('dados_cadastrados').dados) : []
          dados = [...dados, ...dados_cadastrados]
          this.dados = dados
          this.dadosbkp = dados
          this.porcentagem = dados.length
          this.calculaPage(dados)
          // console.log('ok')
          break;
        }
      }
    },
    getPage() {
      const page_atual = !!parseInt(sessionStorage.page_atual) ? parseInt(sessionStorage.page_atual) : 0
      const total_pages = !!parseInt(sessionStorage.total_pages) ? parseInt(sessionStorage.total_pages) : 1

      console.log('page atual', page_atual)
      console.log('total', total_pages)

      const pagiga_a_buscar = parseInt(page_atual) + 1

      if(total_pages !== page_atual) {
        this.$axios.get('/ficha/pagina/'+pagiga_a_buscar)
        .then(res => {
            sessionStorage.fichas = true
            sessionStorage.total_pages = res.data.totalPages
            sessionStorage.total_docs = res.data.totalDocs
            this.$storage.set(`page_${pagiga_a_buscar}`, { dados: JSON.stringify(res.data.docs) } )
            sessionStorage.page_atual = pagiga_a_buscar
            this.normaliza_page()
            this.getPage()
          })
      }
    },
    get_ubs() {
      let ubs = JSON.parse(sessionStorage.ubs_disponiveis)
      ubs.push({
        _id: undefined,
        nome: 'GERAL'
      })
      this.ubs_disponiveis = ubs
    },
    get_paginacao(num_page) {
      this.$store.dispatch('up_charging')

      const query = {
        query_nome: this.pesquisa !== '' ? this.pesquisa: false,
        query_data: this.pesquisa_data !== '' ? this.pesquisa_data : false,
        query_pendentes: this.pesquisa_pendentes ? 'Coletado' : false,
        query_ubs: this.ubs_pesquisa._id
      } 

      this.$axios.post('/ficha/pagina/'+num_page, query)
        .then(res => {
          this.$store.dispatch('down_charging')
          sessionStorage.fichas = true
          sessionStorage.total_pages = res.data.totalPages
          sessionStorage.total_docs = res.data.totalDocs
          this.fichas_atuais = res.data.docs
          // this.$storage.set(`page_${pagiga_anum_page_buscar}`, { dados: JSON.stringify(res.data.docs) } )
          sessionStorage.page_atual = num_page
          this.tamanho_paginacao = res.data.totalPages
          this.total_fichas = res.data.totalDocs
          this.page = res.data.page
          sessionStorage.total_pages = res.data.totalPages
          sessionStorage.total_docs = res.data.totalDocs
          // this.normaliza_page()
          // this.getPage()
        })
    },
  },
  created() {
    // this.get()
    this.get_ubs()
    const page = !!sessionStorage.page ? parseInt(sessionStorage.page) : 1 
    this.get_paginacao(page)
    // this.getPage()
    // this.normaliza_page()
    // console.log("asasdasdsad", this.$storage.length)
    
		const component = this;

		this.handler = function (e) {
			component.$emit('keyup', e);
            
            if(e.altKey && e.key === 'n') {
				component.$router.push("/home/cadastrar-cliente")
            }

            if(e.key === 'Escape') {
                component.$router.go(-1)
            }
            
		}

		window.addEventListener('keyup', this.handler);
	},
	beforeDestroy() {
		window.removeEventListener('keyup', this.handler);
	}
};
</script>
