                                <v-text-field autocomplete="new-field" hint="DIA/MES/ANO" v-mask="['##/##/####']" v-model="dados.inicio_dos_sintomas" label="Início dos sintomas" outlined dense :color="$primaria" class="mr-2 ml-2" required/>
                            </div>

                            <div class="expande-horizontal semquebra">
                                <v-flex xs12 md6>
                                    <v-select outlined dense :color="$primaria" class="mr-2 ml-2" v-model="dados.sintomas" :items="sintomas" multiple required label="Sintomas"/>
                                </v-flex>
