                                <v-select outlined dense class="mr-2 ml-2" :items="tipos_de_teste" v-model="dados.tipo_de_teste" :color="$primaria" label="Tipo de teste"></v-select>
                                <v-select outlined dense class="mr-2 ml-2" :items="resultados_do_teste" v-model="dados.resultado_do_teste" :color="$primaria" label="Resultado do teste"></v-select>
                            </div>

                            <div class="expande-horizontal semquebra">
                                <v-select outlined dense class="mr-2 ml-2" :items="classificacao_final" v-model="dados.classificacao_final" :color="$primaria" label="Classificação final"></v-select>
                                <v-select outlined dense class="mr-2 ml-2" :items="evolucao_do_caso" v-model="dados.evolucao_do_caso" :color="$primaria" label="Evolução do caso"></v-select>
