</template>

<script>
import { mask } from 'vue-the-mask'
export default {
    directives: {
        mask
    },
    data() {
        return {
            options: [
                {
                    icon: "save",
                    nome: "Concluir cadastro",
                    action: () => this.save()
                }
            ],
            outros: false,
            outras_comorbidades: '',
            dados: {
                responsavel: this.$store.getters.user,
                responsavel_id: this.$store.getters.user._id,
                ubs: JSON.parse(sessionStorage.ubs_selecionada),
                ubs_id: JSON.parse(sessionStorage.ubs_selecionada)._id,
                comorbidades: [],
                sintomas: []
            },
            sintomas: [
              'Dor de garganta',
              'Dispneia',
              'Febre',
              'Tosse',
              'outros'
            ],
            opcoes_sim_nao: [
                'SIM',
                'NÃO'
            ],
            sexo: [
                'MASCULINO',
                'FEMININO'
            ],
            raca: [
                'BRANCA',
                'PARDA',
                'PRETA',
                'AMARELA',
                'INDÍGENA',
            ],
            tipos_de_teste: [
                'TESTE RÁPIDO - ANTICORPO',
                'TESTE RÁPIDO - ANTÍGENO',
                'RT - PCR',
                'TESTE SOROLÓGICO'
            ],
            resultados_do_teste: [
                'REAGENTE IGM',
                'REAGENTE IGG',
                'NÃO REAGENTE',
            ],
            classificacao_final: [
                'CONFIRMAÇÃO LABORATORIAL',
                'CONFIRMAÇÃO CLÍNICA EPIDEMIOLÓGICA',
                'DESCARTADO',
            ],
            evolucao_do_caso: [
                'CANCELADO',
                'CURA',
                'EM TRATAMENTO DOMICILIAR',
                'IGNORADO',
                'INTERNADO',
                'INTERNADO EM UTI',
                'ÓBITO',
            ],
            comorbidades: [
                'outros',
                'Doenças respiratórias crônicas descompensadas',
                'Doenças cardíacas',
                'Doenças renais crônicas em estágio avançado',
                'Imunossupressão',
                'Gestante de alto risco',
                'Portador de doenças cromossômicas',
            ],
            estado_do_teste: [
                'Solicitado',
                'Coletado',
                'Concluído'
            ],
            funcionario: '',
            responsavel: [],
            status: ['suspeito', 'confirmado', 'descartado', 'recuperado', 'óbito']
        }
    },
    methods: {
        async save() {
            if(this.$refs.form.validate()) {
                const ficha = await this.$create('ficha', this.dados)
                this.$router.go(-1)
            }
        },
        verificaOutros() {
            let resultado = false

            this.dados.comorbidades.map(item => {
                if(item === 'outros') {
                    resultado = true
                }
            })

            return resultado
        },
        verificaOutrosSintomas() {
            let resultado = false

            this.dados.sintomas.map(item => {
                if(item === 'outros') {
                    resultado = true
                }
            })

            return resultado
        },
    },
    created() {
        this.dados.data_da_notificacao = this.$moment().format('DD/MM/YYYY')
    }
}
</script>

