<template>
    <v-dialog persistent width="350" v-model="abrir">
        <v-card light class="pa-4" style="display: flex; flex-direction: column; width:100%;">
            <div class="expande-horizontal coluna">
                <div class="expande-horizontal centraliza semquebra">
                    <h3 class="text-center pb-2">Adicionar Acompanhamento</h3>
                    <v-spacer></v-spacer>
                    <v-icon class="mb-2" color="black">group</v-icon>
                </div>
                <v-divider></v-divider>
                <v-textarea
                    class="pa-3"
                    v-model="dados.mensagem"
                    label="Mensagem"
                    placeholder="Escreva aqui o resultado do acompanhamento"
                    outlined
                    :color="$primaria"
                />
                <div style="display: flex; width: 100%; justify-content: center; flex-wrap: nowrap">
                    <v-btn text class=" mr-2 white--text" @click="this.fechar" color="red">Cancelar</v-btn>
                    <v-btn class="white--text" @click="add" color="green">Adicionar</v-btn>
                </div>
            </div>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    props: ['abrir', 'fechar', 'id'],
    data() {
        return {
            dados: {},
            options: []
        }
    },
    methods: {
        async add() {
            const dados = {
                mensagem: this.dados.mensagem,
                ficha_id: this.id
            }
            //id ordem
            await this.$create(`ficha_diario_bordo/${this.id}`, dados)
            this.fechar()
        }
    }
}
</script>