                </v-tab-item>

                <!-- <v-tab-item>
                    <v-flex xs12>
                        <div id="impressaoProntuario" style="padding: 10px; display: flex; width: 100%; flex-direction: column">
                            <div style="display: flex; flex-direction: column; margin-bottom: 10px; font-weight: bold">
                                <h3 style="text-align: center">{{ ordem_prontuario }} - PRONTUÁRIO</h3>
                            </div>

                            <table border="1">
                                <tr>
