                                </tr>
                                <tr>
                                    <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">NASCIMENTO</th>
                                    <td style="padding-left: 10px; padding-right: 10px">{{ dados.nascimento }}</td>
                                </tr>
                                <tr>
                                    <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">ENDEREÇO</th>
                                    <td style="padding-left: 10px; padding-right: 10px">{{ dados.endereco }} {{ dados.numero }} {{ dados.bairro }}</td>
                                </tr>
                                <tr>
                                    <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">CONTATO</th>
                                    <td style="padding-left: 10px; padding-right: 10px">{{ dados.contatos }}</td>
                                </tr>
                                <tr>
                                    <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">CPF</th>
                                    <td style="padding-left: 10px; padding-right: 10px">{{ dados.cpf }}</td>
                                </tr>
                            </table>
                        </div>
                        <v-btn @click="imprimeProntuario" color="green" class="ml-2 white--text pa-2"> <v-icon class="mr-2 ">print</v-icon> IMPRIMIR PRONTUÁRIO</v-btn>

                    </v-flex>
                </v-tab-item> -->

                <v-tab-item>
                    <div id="impressaoNotificacao" style="padding: 10px; display: flex; width: 100%; flex-direction: column">
                        <h3 style="text-align: center">NOTIFICAÇÃO - {{ $moment().format('DD/MM/YYYY [às] HH:mm') }} </h3>
                        
                        <table border="1">
                            <tr>
                                <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">PACIENTE</th>
                                <td style="padding-left: 10px; padding-right: 10px">{{ dados.nome }}</td>
                            </tr>
                            <tr>
                                <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">NASCIMENTO</th>
                                <td style="padding-left: 10px; padding-right: 10px">{{ dados.nascimento }}</td>
                            </tr>
                            <tr>
                                <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">ENDEREÇO</th>
                                <td style="padding-left: 10px; padding-right: 10px">{{ dados.endereco }} {{ dados.numero }} {{ dados.bairro }}</td>
                            </tr>
                            <tr>
                                <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">CONTATO</th>
                                <td style="padding-left: 10px; padding-right: 10px">{{ dados.contatos }}</td>
                            </tr>
                            <tr>
                                <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">CPF</th>
                                <td style="padding-left: 10px; padding-right: 10px">{{ dados.cpf }}</td>
                            </tr>
                            <tr>
                                <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">TIPO DE TESTE</th>
                                <td style="padding-left: 10px; padding-right: 10px">{{ dados.tipo_de_teste }}</td>
                            </tr>
                            <tr>
                                <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">DATA DA COLETA</th>
                                <td style="padding-left: 10px; padding-right: 10px">{{ dados.data_da_coleta }}</td>
                            </tr>
                        </table>
                    </div>
                    <v-btn @click="imprimeNotificacao" color="green" class="ml-2 white--text pa-2"> <v-icon class="mr-2 ">print</v-icon> IMPRIMIR NOTIFICAÇÃO</v-btn>
                </v-tab-item>
            </v-tabs-items>
