		<AddDiarioDeBordo :abrir="add_diario_de_bordo" :fechar="() => { this.add_diario_de_bordo = false; this.get('ficha_diario_bordo', this.id) }" :id="this.id" />
    </div>
</template>

<script>
import AddDiarioDeBordo from './add/AddDiarioDeBordo'
import DiarioDeBordo from './tabs/DiarioDeBordo.vue'
import Printd from 'printd'

import { mask } from 'vue-the-mask'
export default {
    props: ['id'],
    directives: {
        mask
    },
    components: {
        AddDiarioDeBordo,
        DiarioDeBordo
    },
    data() {
        return {
            show_printer: false,
            ordem_notificacao: '',
            ordem_prontuario: '',
            options: [
                // {
                //     icon: 'print',
                //     nome: 'Imprimir Prontuário',
                //     action: () => this.imprimeProntuario()
                // },
                {
                    icon: 'print',
                    nome: 'Imprimir Notificação',
                    action: () => this.imprimeNotificacao()
                },
                {
                    icon: 'add',
                    nome: 'Acompanhamento',
                    action: () => this.add_diario_de_bordo = true
                },
                {
                    icon: "save",
                    nome: "Salvar Alterações",
                    action: () => this.save()
                }
            ],
            tab: 0,
            ficha_diario_bordo: [],
            add_diario_de_bordo: false,
            dados: {
                responsavel: this.$store.getters.user,
                responsavel_id: this.$store.getters.user._id,
                comorbidades: [],
                sintomas: [],
                ubs: {
                    nome: ''
                }

            },
            ubs_disponiveis: [],
            d: null,
            sintomas: [
              'Dor de garganta',
              'Dispneia',
              'Febre',
              'Tosse',
              'outros'
            ],
            opcoes_sim_nao: [
                'SIM',
                'NÃO'
            ],
            sexo: [
                'MASCULINO',
                'FEMININO'
            ],
            raca: [
                'BRANCA',
                'PARDA',
                'PRETA',
                'AMARELA',
                'INDÍGENA',
            ],
            tipos_de_teste: [
                'TESTE RÁPIDO - ANTICORPO',
                'TESTE RÁPIDO - ANTÍGENO',
                'RT - PCR',
                'TESTE SOROLÓGICO'
            ],
            resultados_do_teste: [
                'REAGENTE IGM',
                'REAGENTE IGG',
                'NÃO REAGENTE',
            ],
            classificacao_final: [
                'CONFIRMAÇÃO LABORATORIAL',
                'CONFIRMAÇÃO CLÍNICA EPIDEMIOLÓGICA',
                'DESCARTADO',
            ],
            evolucao_do_caso: [
                'CANCELADO',
                'CURA',
                'EM TRATAMENTO DOMICILIAR',
                'IGNORADO',
                'INTERNADO',
                'INTERNADO EM UTI',
                'ÓBITO',
            ],
            comorbidades: [
                'outros',
                'Doenças respiratórias crônicas descompensadas',
                'Doenças cardíacas',
                'Doenças renais crônicas em estágio avançado',
                'Imunossupressão',
                'Gestante de alto risco',
                'Portador de doenças cromossômicas',
            ],
            estado_do_teste: [
                'Solicitado',
                'Coletado',
                'Concluído'
            ],
            funcionario: '',
            responsavel: [],
            status: ['suspeito', 'confirmado', 'descartado', 'recuperado', 'óbito']
        }
    },
    methods: {
        async get(collection, id) {
            this[`${collection}`] = await this.$list(`${collection}/${id}`)
        },
        async save() {
            if(this.$refs.form.validate()) {
                await this.$update(`ficha/${this.id}`, this.dados)
                this.$router.go(-1)
            }
        },
        async imprimeProntuario() {
            this.tab = 2
            const dados = {
                ubs: JSON.parse(sessionStorage.ubs_selecionada),
                ubs_id: JSON.parse(sessionStorage.ubs_selecionada)._id,
                data: this.$moment().format('DD/MM/YYYY'),
                hora: this.$moment().format('HH:mm'),
                ficha: this.dados,
                ficha_id: this.id
            }

            const ordem = await this.$create('/ficha_prontuario/'+this.id, dados)
            this.ordem_prontuario = ordem.ordem
            this.registraProntuario()
            
        },
        imprimeNotificacao() {
            this.tab = 3
            const dados = {
                ubs: JSON.parse(sessionStorage.ubs_selecionada),
                ubs_id: JSON.parse(sessionStorage.ubs_selecionada)._id,
                data: this.$moment().format('DD/MM/YYYY'),
                hora: this.$moment().format('HH:mm'),
                ficha: this.dados,
                ficha_id: this.id
            }

            this.$store.dispatch('up_charging')

            this.$axios.post('/ficha_notificacao/'+this.id, dados)
                .then(res => {
                    this.$store.dispatch('down_charging')
                    this.ordem_notificacao = res.data.ordem
                    this.registraNotificacao()
                })
                .catch(e => {
                    this.$store.dispatch('down_charging')
                    this.$store.dispatch('snackbar_error', e)
                })
        },
        async registraProntuario() {
            setTimeout(() => this.d.print( document.getElementById('impressaoProntuario')), 100)    
        },
        async registraNotificacao() {
            setTimeout(() => this.d.print( document.getElementById('impressaoNotificacao')), 100)
        },
        verificaOutros() {
            let resultado = false

            this.dados.comorbidades.map(item => {
                if(item === 'outros') {
                    resultado = true
                }
            })

            return resultado
        },
        verificaOutrosSintomas() {
            let resultado = false

            this.dados.sintomas.map(item => {
                if(item === 'outros') {
                    resultado = true
                }
            })

            return resultado
        },
        async get_ficha() {
            let dados = await this.$show(`ficha/${this.id}`)
            console.log("dados", dados)
            let dados_novos = dados
            if(dados.ubs_id === undefined) {
                dados_novos.ubs = {
                    nome: 'Aguardando'
                }
                this.dados = dados_novos
            }
        }

    },
    mounted() {
        this.d = new Printd()


        const { contentWindow } = this.d.getIFrame()
    },
    async created() {
        this.ubs_disponiveis = JSON.parse(sessionStorage.ubs_disponiveis)
        this.get_ficha()
    }
}
</script>

