<template>
  <div>
    <v-navigation-drawer style="background:#fff" class="elevation-1" v-model="drawer" app clipped width="270">
      <v-card class="elevation-0" style="border-radius: 0px; margin-bottom: 0px;" color="white">
        <div class="semQuebra expande-horizontal">
          <v-list light style="width: 100%" class="pa-0 ma-0">
            <v-list-item @click="() => {}">
              <v-avatar color="white" class="elevation-1 mr-2 mt-2 mb-2">
                <v-img src="images/icon.png"></v-img>
              </v-avatar>
              <v-list-item-content>
                <v-list-item-title class="black--text text-capitalize">
                  {{ $store.getters.user.nome }}
                </v-list-item-title>
                <v-list-item-subtitle style="font-size: 10pt" class="grey--text">
                  {{ $moment().locale('pt-br').format('DD/MM/YYYY') }}
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </div>
      </v-card>

      <div class="mr-1 ml-1" style="border-bottom: 1px solid #e2e2e2"></div>

      <v-list dark style="border-radius: 0px" nav dense color="white" class="pa-1 ma-0">
        <v-subheader class="grey--text">Opções</v-subheader>

        <v-list-item active-class="menuAtivoHome" to="/cliente/listar-contrato">
          <v-avatar>
            <v-icon :color="$route.path === '/cliente/listar-contrato' ? 'white' : $primaria">
              description
            </v-icon>
          </v-avatar>
          <v-list-item-content>
            <v-list-item-title :class="$route.path === '/cliente/listar-contrato' ? 'white--text' : 'grey--text'" class="fonteCorpo font-weight-bold" >
              Contratos
            </v-list-item-title>
          </v-list-item-content>
          <v-list-item-action>
                <v-btn icon>
                  <v-icon>arrow_right</v-icon>
                </v-btn>
              </v-list-item-action>
        </v-list-item>

        <v-list-item active-class="menuAtivoHome" to="/cliente/listar-ordem">
          <v-avatar>
            <v-icon :color="$route.path === '/cliente/listar-ordem' ? 'white' : $primaria">
              work
            </v-icon>
          </v-avatar>
          <v-list-item-content>
            <v-list-item-title :class="$route.path === '/cliente/listar-ordem' ? 'white--text' : 'grey--text'" class="fonteCorpo font-weight-bold" >
              Ordens de Serviço
            </v-list-item-title>
          </v-list-item-content>
          <v-list-item-action>
                <v-btn icon>
                  <v-icon>arrow_right</v-icon>
                </v-btn>
              </v-list-item-action>
        </v-list-item>

        <v-divider></v-divider>

        <v-list-item @click="sair" active-class="menuAtivoHome">
          <v-avatar>
            <v-icon color="red darken-3">swap_horizontal_circle</v-icon>
          </v-avatar>
          <v-list-item-content >
            <v-list-item-title class="fonteCorpo grey--text"> Sair </v-list-item-title>
          </v-list-item-content>
        </v-list-item>

      </v-list>
    </v-navigation-drawer>
    <v-content>
      <transition name="router-anim" >
        <router-view></router-view>
      </transition>
    </v-content>
  </div>
</template>

<script>
export default {
  props: {
    source: String
  },
  data() {
    return {
      permissao: {},
    }
  },
  computed: {
    drawer() {
      return this.$store.getters.drawer
    },
  },
  methods: {
    sair() {
      sessionStorage.clear()
      this.$router.push('/')
    },
    verificaUsuarioLogado() {
      if(sessionStorage.perfil === undefined) {
        this.$router.push('/')
      }
    }
  },
  created() {
    this.verificaUsuarioLogado()
  }
};
</script>

<style>
#keep .v-navigation-drawer__border {
  display: none;
}

::-webkit-scrollbar {
  width: 2px;
  border-radius: 10px;
  background: rgba(180, 179, 179, 0);
}
::-webkit-scrollbar-thumb {
  background: #F9A825;
  border-radius: 10px;
}

.menuAtivo {
  color: #ffffff;
  border-radius: 5px;
  margin-right: 5px;
  font-weight: bold;
}

  .router-anim-enter-active {
  animation: coming 0.4s;
  animation-delay: 0.4s;
  opacity: 0;
}

.router-anim-leave-active {
  animation: going 0.4s;
}

@keyframes going {
  from {
    scale: 1;
    opacity: 1;
  }
  to {
    scale: 0.7;
    opacity: 0.1;
  }
}

@keyframes coming {
  from {
    scale: 0.7;
    opacity: 0.1;
  }
  to {
    scale: 1;
    opacity: 1;
  }
}
</style>