<template>
  <div
    class="principal"
  >
    <div
      class="HomePublic bg"
      style="flex-direction: column; justify-content: center; align-items: center;"
    >
      <v-card
        v-if="lock"
        color="transparent"
        class="cardLogin elevation-0 fadeInUp ma-1 pr-7 pl-7"
        style="width: 430px; border-radius: 6px; opacity: 1"
      >
        <div style="display: flex; width: 100%; justify-content: center">
          <v-avatar class="elevation-3" size="100" style="border: 5px solid #ffff">
            <v-img
              class="mb-3 mt-3"
              src="images/icon.png"
              alt="logo"
            />
          </v-avatar>
        </div>

        <v-flex v-if="loginEmpresa" xs12 class="pa-2">
          <h3 class="fonteCorpo text-center mt-3 mb-5" :style="`color: ${$primaria}`"> Controle Corona Vírus </h3>
          <v-flex xs12>
            <v-select autofocus autocomplete="new-login" :color="$primaria" return-object item-text="nome" v-model="ubs_selecionada" :items="ubs" label="Em qual ubs deseja entrar?" />
          </v-flex>
          <v-flex v-if="ubs_selecionada !== ''" xs12>
            <v-text-field autofocus autocomplete="new-login" :color="$primaria" v-model="email" label="Login" />
          </v-flex>
          <v-flex v-if="ubs_selecionada !== ''" xs12>
            <v-text-field
              :color="$primaria"
              :append-icon="showPass ? 'visibility' : 'visibility_off'"
              :type="showPass ? 'text' : 'password'"
              @click:append="showPass = !showPass"
              @keydown.enter="login"
              v-model="senha"
              label="Senha"
            />
          </v-flex>
          <v-flex v-if="ubs_selecionada !== ''" xs12>
            <div class="expande-horizontal centraliza">
              
              <v-btn color="red" class="white--text" @click="login">
                <h4>Entrar</h4>
                <v-icon class="ml-2">post_add</v-icon>
              </v-btn>
            </div>
          </v-flex>
        </v-flex>

      </v-card>
      <v-card
        v-else
        color="transparent"
        class="cardLogin elevation-0 fadeInUp ma-1 pr-7 pl-7"
        style="width: 430px; border-radius: 6px; opacity: 1"
      >
        <div style="display: flex; width: 100%; justify-content: center">
          <v-avatar class="elevation-3" size="100" style="border: 5px solid #ffff">
            <v-img
              class="mb-3 mt-3"
              src="images/icon.png"
              alt="logo"
            />
          </v-avatar>
        </div>

        <v-flex v-if="loginEmpresa" xs12 class="pa-2">
          <h3 class="fonteCorpo text-center mt-3 mb-5" :style="`color: ${$primaria}`"> Controle Corona Vírus </h3>
          <v-flex xs12>
            <v-text-field v-mask="['###.###.###-##']" autofocus autocomplete="new-login" :color="$primaria" v-model="cpf" label="CPF" />
          </v-flex>
          <v-flex xs12>
            <div class="expande-horizontal centraliza">
              <v-btn color="red" class="white--text" @click="consultar">
                <h4>Consultar</h4>
                <v-icon class="ml-2">description</v-icon>
              </v-btn>
            </div>
          </v-flex>
          <v-flex xs12>
            <div class="mt-3 expande-horizontal centraliza">
              <v-btn icon color="red" class="white--text" @click="lock=!lock">
                <v-icon>lock</v-icon>
              </v-btn>
            </div>
          </v-flex>
        </v-flex>

      </v-card>
    </div>
    <v-dialog fullscreen v-model="dialog_ficha">
      <v-card class="expande-horizontal centraliza coluna">
        <v-app-bar class="elevation-0" color="transparent" top>
          <v-btn rounded text @click="dialog_ficha=!dialog_ficha">
            <v-icon class="mr-2">arrow_back</v-icon>
            fechar
          </v-btn>
        </v-app-bar>
        <div class="expande-horizontal centraliza coluna" style="height: 100vh">

          <img width="72" class="mb-3" src="https://upload.wikimedia.org/wikipedia/commons/thumb/7/7d/Bras%C3%A3o_de_Macap%C3%A1.svg/1200px-Bras%C3%A3o_de_Macap%C3%A1.svg.png" alt="logo">
          
          <h3 class="mt-3 mb-3">Teste Rápido SARS-COV2</h3>
          
          <table style="width: 70%" border="1">
              <tr>
                  <th style="text-align: left; width: 80px; padding-left: 10px; padding-right: 10px">PACIENTE</th>
                  <td style="padding-left: 10px; padding-right: 10px">{{ ficha.nome }}</td>
              </tr>
              <tr>
                  <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">NASCIMENTO</th>
                  <td style="padding-left: 10px; padding-right: 10px">{{ ficha.nascimento }}</td>
              </tr>
              <tr>
                  <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">ENDEREÇO</th>
                  <td style="padding-left: 10px; padding-right: 10px">{{ ficha.endereco }} {{ ficha.numero }} {{ ficha.bairro }}</td>
              </tr>
              <tr>
                  <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">CONTATO</th>
                  <td style="padding-left: 10px; padding-right: 10px">{{ ficha.contatos }}</td>
              </tr>
              <tr>
                  <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">CPF</th>
                  <td style="padding-left: 10px; padding-right: 10px">{{ ficha.cpf }}</td>
              </tr>
              <tr>
                  <th style="text-align: left; width: 40px; padding-left: 10px; padding-right: 10px">RESULTADO DO TESTE</th>
                  <td style="padding-left: 10px; padding-right: 10px">{{ ficha.resultado_do_teste }}</td>
              </tr>
          </table>
          <img class="mt-6" src="https://firebasestorage.googleapis.com/v0/b/deliverysapp.appspot.com/o/assinatura_favicon.png?alt=media&token=793c3ba7-669e-408a-9690-15d0c04908b7" width="60" alt="">
          <h3 class="text-center">Felipe Santos Pereira</h3>
          <span>CRBM 2908-PA</span>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mask } from 'vue-the-mask'
export default {
  directives: {
    mask
  },
  data() {
    return {
      lock: false,
      cnpj_on: false,
      random_um: 0,
      random_dois: 0,
      captcha: true,
      loginCliente: false,
      loginEmpresa: true,
      resultado: '',
      email: '',
      senha: "",
      cpf: "",
      ubs_selecionada: '',
      ubs: [],
      showPass: false,
      entrando: false,
      error: false,
      errorMessage: "",
      alteraSenhaDialog: false,
      enviaLinkRecuperacao: false,
      carregandoLogin: false,
      dialog_ficha: false,
      ficha: {}
    };
  },
  methods: {
    getPage() {
      const page_atual = parseInt(sessionStorage.page_atual)
      const total_pages = parseInt(sessionStorage.total_pages)

      const pagiga_a_buscar = parseInt(page_atual) + 1

      if(page_atual < total_pages) {
        this.$axios.get('/ficha/pagina/'+pagiga_a_buscar)
        .then(res => {
            sessionStorage.fichas = true
            sessionStorage.total_pages = res.data.totalPages
            sessionStorage.total_docs = res.data.totalDocs
            sessionStorage.page_atual = res.data.page
            this.$storage.set(`page_${pagiga_a_buscar}`, { dados: JSON.stringify(res.data.docs) })
            sessionStorage.page_atual = pagiga_a_buscar
            this.getPage()
          })
      }
    },
    getFichas() {
      this.$axios.get('/ficha/pagina/1')
        .then(res => {
          sessionStorage.fichas = true
          sessionStorage.total_pages = res.data.totalPages
          sessionStorage.total_docs = res.data.totalDocs
          this.$storage.set(`page_1`, { dados: JSON.stringify(res.data.docs) } )
          sessionStorage.page_atual = 1
          this.getPage()
        })
    },
    consultar() {
      this.$store.dispatch('up_charging')

      const dados = {
        cpf: this.cpf
      }

      this.$axios.post('/contulta_ficha', dados)
        .then((res) => {
          if(res.data.length > 0) {
            //tem ficha
            this.dialog_ficha = true
            this.ficha = res.data[0]
            this.$store.dispatch('down_charging')

          } else {
            //não tem ficha
            this.$store.dispatch('snackbar_info', 'não há fichas para este cpf')
            this.$store.dispatch('down_charging')
          }
        })
        .catch(e => {
          this.$store.dispatch('snackbar_error', e.response.data.message)
          this.$store.dispatch('down_charging')
        })
    },
    resetLogin() {
      this.loginCliente = false
      this.loginEmpresa = false
    },
    login() {
      this.$store.dispatch('up_charging')

      const dados = {
        email: this.email,
        senha: this.senha
      }

      this.$axios.post('/session', dados)
        .then((res) => this.posLogin(res.data))
        .catch(e => {
          this.$store.dispatch('snackbar_error', e.response.data.message)
          this.$store.dispatch('down_charging')
        })
    },
    loginCli() {
      this.$store.dispatch('up_charging')

      const dados = {
        cpf: this.cpf
      }

      this.$axios.post('/session_cli', dados)
        .then((res) => this.posLogin(res.data))
        .catch(e => {
          this.$store.dispatch('snackbar_error', e.response.data.message)
          this.$store.dispatch('down_charging')
        })
    },
    posLogin(perfil) {
      if(parseInt(perfil.user.block) === 1) {
        this.$store.dispatch('snackbar_info', 'Conta Bloqueada!')
        this.$store.dispatch('down_charging')
      } //else if(!perfil.user.conta_confirmada) {
        //this.$store.dispatch('snackbar_info', 'Esta conta ainda não está ativa em nossa plataforma, um email com o link de ativação da conta foi enviado para '+perfil.user.email)
        //this.$store.dispatch('down_charging')
      //} 
      else {
        sessionStorage.perfil = JSON.stringify(perfil)
        sessionStorage.ubs_selecionada = JSON.stringify(this.ubs_selecionada)
        this.$store.dispatch('down_charging')
        if(this.loginEmpresa) {
          this.$router.push('/home/listar-ficha')
        } else {
          this.$router.push('/cliente/listar-contrato')
        }
        location.reload()
      }
    },
    enviaLinkRecuperacaoDeSenha() {
      // inserir lógica de recuperação de senha aqui
      this.$store.dispatch('up_charging')

      const dados = {
        email: this.email
      }

      this.$axios.post('/esqueci_senha', dados)
        .then((res) => {
          this.$store.dispatch('down_charging')
          this.$store.dispatch('snackbar_success', res.data.message)
          this.alteraSenhaDialog = false
        })
        .catch(e => {
          this.$store.dispatch('snackbar_error', e.response.data.message)
          this.$store.dispatch('down_charging')
        })
    },
    goToCadastro() {
      this.$router.push("/cadastro/sem");
    },
    isAvailableSuccess(result) {
      /*
      result depends on device and os. 
      iPhone X will return 'face' other Android or iOS devices will return 'finger'  
      */
      this.$store.dispatch('snackbar_success', result)
    },
    isAvailableError(error) {
      // 'error' will be an object with an error code and message
      this.$store.dispatch('snackbar_info', error.message)

    }
  },
  async created() {
    const dados = await this.$list('ubs')
    this.ubs = dados
    sessionStorage.ubs_disponiveis = JSON.stringify(dados)
    // this.getFichas()
    
    // Fingerprint.isAvailable(this.isAvailableSuccess, this.isAvailableError);
  }
};
</script>

<style>
  .bg {
    background: linear-gradient(#eee9e2, #ececea);
  }
  .principal {
    background: linear-gradient(to right, #f7971e, #ffd200);
    display: flex;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .fonte {
    font-family: "Crimson Text", serif;
    font-size: 25pt;
  }

  .loginForm {
    border-radius: 5px;
    display: flex;
    width: 100%;
    opacity: 0.9;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background-color: #fdfcfc;
  }

  .cardLogin {
    animation-duration: 400ms;
  }

.meio {
    display: flex;
    width: 100%;
    flex-direction: column;
  }
  .snack {
    animation-duration: 600ms;
  }
</style>
